.site-video {
  position: relative;
  overflow: hidden;
}
.site-video::before {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  content: "";
  z-index: 1;
  border-image: fill 0 linear-gradient(180deg, #000, rgba(0, 0, 0, 0));
  transition: opacity var(--transition-speed-slow) ease-in-out;
}
.site-video--playing {
  --btn-animation: play-fade-in;
  --btn-events: none;
}
.site-video--playing::before {
  opacity: 0;
  pointer-events: none;
}
.site-video--paused {
  --btn-animation: play-fade-out;
  --btn-events: auto;
}
.site-video--paused::before {
  opacity: 1;
}
.site-video__btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  display: flex;
  align-items: center;
  animation-name: var(--btn-animation, none);
  animation-fill-mode: forwards;
  animation-duration: var(--transition-speed-slow);
  pointer-events: var(--btn-events, auto);
}
.site-video__btn-text {
  text-transform: uppercase;
  color: var(--white-color);
}
.site-video__btn-text--order {
  order: -1;
  margin-right: 12px;
}
.site-video__btn-text:last-child {
  margin-left: 12px;
}
.site-video__btn-wrap {
  padding: 0;
  color: inherit;
  background-color: transparent;
  border: none;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 110px;
  height: 110px;
}
.site-video__btn-line {
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 110px;
  height: 110px;
  transform-origin: center center;
  border-radius: 100%;
  border: 1px solid var(--gray-50-color);
  transition: width 0.3s ease-in-out, height 0.3s ease-in-out, opacity 0.2s ease-in-out;
  animation-delay: 0.1s;
}
.site-video__btn-circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 100%;
  width: 80px;
  height: 80px;
  background-color: rgba(255, 255, 255, 0.1);
  transition: width 0.3s ease-in-out, height 0.3s ease-in-out;
  animation-delay: 0.4s;
}
.site-video__btn-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.site-video__content {
  display: flex;
  transition: opacity var(--transition-speed-slow) ease-in-out;
  height: 100%;
}
@media (hover: hover) {
  .site-video:hover::before {
    opacity: 0;
  }
  .site-video__btn-wrap:hover ~ .site-video__btn-text {
    animation: fadeOut 0.2s ease-in-out forwards;
    animation-delay: 0.6s;
  }
  .site-video__btn-wrap:hover .site-video__btn-line {
    animation: growLine 0.3s ease-in-out forwards, fadeOut 0.2s ease-in-out forwards;
    animation-delay: 0.1s, 0.6s;
  }
  .site-video__btn-wrap:hover .site-video__btn-circle {
    animation: growCircle 0.3s ease-in-out forwards;
    animation-delay: 0.5s;
  }
}

