  .about-slider__controls {
    bottom: 48px;
  }
}
@media (min-width: 1920px) {
  .about-slider__controls {
    bottom: 64px;
  }
}

.about-cooperation {
  --dark-bg-color: rgba(0, 0, 0, 0);
  --light-bg-color: var(--black-color);
  background-color: var(--black-color);
  position: relative;
  padding: 192px 0 48px;
}
.about-cooperation::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  border-image: fill 0 linear-gradient(180deg, var(--dark-bg-color), var(--light-bg-color));
  background-color: var(--black-color);
  background-image: url("../img/img-about-cooperation.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  min-height: 300px;
}
@media (min-width: 992px) {
  .about-cooperation::before {
    width: 36dvw;
    height: 100%;
  }
}
@media (min-width: 1920px) {
  .about-cooperation::before {
    width: 35dvw;
  }
}
@media (min-width: 992px) {
  .about-cooperation {
    padding: 96px 0;
  }
}
@media (min-width: 1920px) {
  .about-cooperation {
    padding: 128px 0;
  }
}
.about-cooperation__title {
  color: var(--white-color);
  margin-bottom: 32px;
}
@media (min-width: 1920px) {
  .about-cooperation__title {
    margin-bottom: 48px;
  }
}
.about-cooperation__meta {
  color: var(--white-color);
  line-height: 1.1;
  margin-bottom: 32px;
}
@media (min-width: 1920px) {
  .about-cooperation__meta {
    margin-bottom: 48px;
  }
}
.about-cooperation__btn {
  --btn-bg: var(--white-color);
  --btn-padding: 16px 62px;
}
@media (min-width: 1920px) {
  .about-cooperation__btn {
    --btn-padding: 24px 62px;
  }
}
@media (hover: hover) {
  .about-cooperation__btn:hover {
    --btn-hover-bg: transparent;
    --btn-hover-color: var(--white-color);
    border-color: var(--white-color);
  }
}

.about-video {
  max-width: 100%;
}